import React, {Component} from 'react';
import { CSSTransitionGroup } from 'react-transition-group';

import cup from '../worldcup.jpg';
import sophia from '../sophia.jpg';
import steemit from '../steemit.png';
import storm from '../storm.jpg';
import protesta from '../protestas.jpg';

var ReactCSSTransitionGroup =require('react-addons-css-transition-group');

let count=0;

class Main extends Component{

  constructor(props) {
    super(props);
    this.state = {titulares:[""] };
    this.handleAdd = this.handleAdd.bind(this);
  }


  handleAdd(i) {
    const listTitulares=["El precio del sbd aumenta 30% el día de hoy"
                         ,"Sophia sorprende nuevamente"
                         ,"Perú abré mercado para recibir pagos en criptomonedas"];

    const newTitulares =this.state.titulares.slice();

    newTitulares.push(listTitulares[i]);

    this.setState({titulares:newTitulares});
  }

  handleRemove(i) {
    let newTitulares = this.state.titulares.slice();
    newTitulares.splice(i, 1);
    this.setState({titulares: newTitulares});

  }

  componentDidMount() {
    setInterval(()=> {
      count=count+1;
      console.log(count);
      if (count==1){
        this.handleAdd(0);
        count=count+1;

      }
      else if (count>1){
        count=count+1;
        if(count==4){
          this.handleRemove(1);
        }
        else if (count==6){
          this.handleAdd(1);
        }
        else if (count==10)
        {
          this.handleRemove(1);
        }
        else if (count==12)
        {
          this.handleAdd(2);
        }
        else if (count==16){
          this.handleRemove(1);
          count=0;
        }

      }


      }, 2000);

  }
  render(){

    const titulares =  this.state.titulares.map((titular, i) => (

    <p onClick={() => this.handleRemove(i)}>
       {titular}
     </p>

   ));
  
    return(
      <div className="main">

        <div id="Titulares" className="clearfix">
          <h2 >Titulares</h2>

          <ReactCSSTransitionGroup
            transitionName="titulares"
            transitionEnterTimeout={500}
            transitionLeaveTimeout={300}>
            {titulares}
          </ReactCSSTransitionGroup>


        </div>{/*Titulares*/}


        <div className="contenedor clearfix">

          <main className="main-cuerpo clearfix">

            <div className="categoria">
            <h3>Categoría</h3>

            <article>
              <div className="imagen">
                <img src={cup}/>
              </div> {/*Imagen*/}

              <h4>Copa mundial Rusia 2018</h4>
              <p>Se acerca glorioso el magno evento esperado por muchos
              durante años. Los organizadores de la FIFA2018 han anunciado que ...
              <a href="#">Leer más</a> </p>

            </article>
            </div>{/*Categoria*/}

            <div className="categoria">
            <h3>Sucesos</h3>
            <article>
              <div className="imagen">
                <img src={sophia}/>
              </div> {/*Imagen*/}
              <h4>Sophia sorprende nuevamente</h4>
              <p>La primer robot humaniode es una realidad. Se llama Sophia
              y causa conmoción con cada presentación, lo último ha sido... <a href="#">Leer más</a> </p>


            </article>
            </div>{/*Categoria*/}

          </main>

          <aside className="aside">

              <h3>Top 5 noticias</h3>

            <div id="top-noticias">


              <div className="noticiaTop">
                <div className="imagen">
                  <img src={storm}/>
                </div>
                <h5>Tormenta en Chile</h5>
              </div>
              <div className="noticiaTop">
                <div className="imagen">
                  <img src={steemit}/>
                </div>
                <h5>La red social que paga a sus autores</h5>
              </div>
              <div className="noticiaTop">
                <div className="imagen">
                  <img src={sophia}/>
                </div>
                <h5>Sophia: Robot humanoide</h5>
              </div>
              <div className="noticiaTop">
                <div className="imagen">
                  <img src={sophia}/>
                </div>
                <h5>Trump acusa a dirigentes politicos</h5>
              </div>
              <div className="noticiaTop">
                <div className="imagen">
                  <img src={protesta}/>
                </div>
                <h5>Venezuela protesta</h5>
              </div>
            </div>

          </aside>


        </div>

      </div>
    );

  }

}

export default Main;



