import React, {Component} from 'react';
import protestas from '../protestas.jpg';
import trump from '../trump.jpg';





class Pres extends Component{


  render(){
    let presText="Confía en el tiempo, que suele dar dulces salidas a  muchas amargas dificultades";




    return(
      <div className="pres clearfix">
        <div id="gradiente">
        <div id="texto-pres">

          <p>"{presText}"</p>
          <p id="autor">Miguel de Cervantes</p>
        </div>
        </div>
        <div id="contenedor">
        <div className="im" style={{ backgroundImage: `url(${protestas})` }} >

        </div>

          <div className="im" style={{ backgroundImage: `url(${trump})` }} >
          </div>
          </div>



      </div>

    );

  }

}

export default Pres;



