import React, {Component} from 'react';
import selector from '../selector.jpg';
var ReactCSSTransitionGroup =require('react-addons-css-transition-group');

class Header extends Component{

  constructor(props){
    super(props);
    this.state={menu:<p>parrafo</p>};
    this.createMenu=this.createMenu.bind(this);
  }

  createMenu(){
    this.setState({menu:<ul>
      <li><h3>Política</h3></li>

        <li><a href="#">Latinoamérica</a></li>
        <li><a href="#">Europa</a></li>
        <li><a href="#">América</a></li>
        <li><a href="#">Ásia</a></li>

    </ul>})


  }





  render(){

    let fecha=new Date();

    function getDia(){
      const days=["Lunes","Martes","Miércoles","Jueves","Viernes","Sábado","Domingo"];
      let dayName;
      dayName="";
      for (var i = 0; i < days.length; i++) {
        if((fecha.getDay()-1)==i){
          dayName=days[i];
          return dayName;
          break;
        }
      }
    }

    function getMes(){
      let mes;
      mes="";
      const months=["Enero","Febrero","Marzo","Abril","Mayo","Junio","Julio",
                    "Agosto","Septiembre","Octubre","Noviembre","Diciembre"];
      for (var i = 0; i < months.length; i++) {
        if(fecha.getMonth()==i){
          mes=months[i];
          return mes;
          break;
        }
      }
    }
    let fechaString=getDia()+" "+fecha.getDate()+" de "+getMes()+" de "+fecha.getFullYear().toString();



    return(

    <div className="header">

      <div className="logo">
      <h1 className="logo">Logo</h1>
      </div>

      <div className="contenedor clearfix">

        <span id="fecha">{fechaString}</span>

        <div className="nav-principal ">

          <nav>
            <ul>
                <li className="clearfix" ><img style={{ opacity: 0.2 }} src={selector}/>
                <ul>
                  <li><h3>Política</h3></li>

                    <li><a href="#">Latinoamérica</a></li>
                    <li><a href="#">Europa</a></li>
                    <li><a href="#">América</a></li>
                    <li><a href="#">Ásia</a></li>

                </ul>
                </li>
                <li><img style={{ opacity: 0.4 }} src={selector}/>
                <ul>
                  <li><h3>Deportes</h3></li>
                </ul>
                </li>
                <li><img style={{ opacity: 0.6 }} src={selector}/>
                <ul>
                  <li><h3>Economía</h3></li>
                </ul>
                </li>
                <li><img style={{ opacity: 0.8 }} src={selector}/>
                <ul>
                  <li><h3>Cultura</h3></li>
                </ul>

                </li>
                <li><img style={{ opacity: 1 }} src={selector}/>
                <ul>
                  <li><h3>Ciencia</h3></li>
                </ul>
                </li>
            </ul>
          </nav>

        </div> {/*NavPrincipal*/}

        <div id="nombre">
          <h2>BLOQUE64</h2>
        </div>

      </div> {/*contenedor*/}

    </div>

    );


  }

}

export default Header;



